<template>
  <div>
    <slot />
  </div>
</template>

<script>
import throttle from 'lodash/throttle';

export default {
  name: 'VirtualScrollTable',
  props: {
    data: {
      type: Array,
      required: true,
    },
    height: {
      type: Number,
      default: 60,
    },
    buffer: {
      type: Number,
      default: 500,
    },
    keyProp: {
      type: String,
      default: 'id',
    },
    throttleTime: {
      type: Number,
      default: 100,
    },
  },
  data() {
    return {
      sizes: {}, // 
    };
  },
  computed: {
    // itemkey
    offsetMap({ keyProp, height, sizes, data }) {
      const res = {};
      let total = 0;
      for (let i = 0; i < data.length; i++) {
        const key = data[i][keyProp];
        res[key] = total;

        const curSize = sizes[key];
        const size = typeof curSize === 'number' ? curSize : height;
        total += size;
      }
      return res;
    },
  },
  watch: {
    data() {
      this.update();
    },
  },
  created() {
    this.$nextTick(() => {
      this.initData();
    });
  },
  beforeDestroy() {
    if (this.scroller) {
      this.scroller.removeEventListener('scroll', this.onScroll);
      window.removeEventListener('resize', this.onScroll);
    }
  },
  methods: {
    // 
    initData() {
      // 
      this.renderData = [];
      // 
      this.top = undefined;
      this.bottom = undefined;
      // 
      this.start = 0;
      this.end = undefined;

      this.scroller = this.$el.querySelector('.q-table__body-wrapper');

      // 
      setTimeout(() => {
        this.handleScroll();
      }, 100);

      // 
      this.onScroll = throttle(this.handleScroll, this.throttleTime);
      this.scroller.addEventListener('scroll', this.handleScroll);
      window.addEventListener('resize', this.onScroll);
    },

    // 
    updateSizes() {
      const rows = this.$el.querySelectorAll(
        '.q-table__body > tbody > .q-table__row'
      );

      Array.from(rows).forEach((row, index) => {
        const item = this.renderData[index];
        if (!item) return;

        const key = item[this.keyProp];
        const offsetHeight = row.offsetHeight;

        if (this.sizes[key] !== offsetHeight) {
          this.$set(this.sizes, key, offsetHeight);
        }
      });
    },

    // 
    handleScroll(shouldUpdate = true) {
      // 
      this.updateSizes();
      // renderData
      this.calcRenderData();
      // 
      this.calcPosition();
      shouldUpdate && this.updatePosition();
      // 
      this.$emit('change', this.renderData, this.start, this.end);
    },

    // offsetTop
    getOffsetTop(index) {
      const item = this.data[index];
      if (item) {
        return this.offsetMap[item[this.keyProp]] || 0;
      }
      return 0;
    },

    // 
    getSize(index) {
      const item = this.data[index];
      if (item) {
        const key = item[this.keyProp];
        return this.sizes[key] || this.height;
      }
      return this.height;
    },

    // 
    calcRenderData() {
      const { scroller, data, buffer } = this;
      // 
      const top = scroller.scrollTop - buffer;
      const bottom = scroller.scrollTop + scroller.offsetHeight + buffer;

      // 
      let l = 0;
      let r = data.length - 1;
      let mid = 0;
      while (l <= r) {
        mid = Math.floor((l + r) / 2);
        const midVal = this.getOffsetTop(mid);
        if (midVal < top) {
          const midNextVal = this.getOffsetTop(mid + 1);
          if (midNextVal > top) break;
          l = mid + 1;
        } else {
          r = mid - 1;
        }
      }

      // 
      let start = mid;
      let end = data.length - 1;
      for (let i = start + 1; i < data.length; i++) {
        const offsetTop = this.getOffsetTop(i);
        if (offsetTop >= bottom) {
          end = i;
          break;
        }
      }

      // 1
      if (start % 2) {
        start = start - 1;
      }

      this.top = top;
      this.bottom = bottom;
      this.start = start;
      this.end = end;
      this.renderData = data.slice(start, end + 1);
      console.log('renderData', this.renderData);
    },

    // 
    calcPosition() {
      const last = this.data.length - 1;
      // 
      const wrapHeight = this.getOffsetTop(last) + this.getSize(last);
      // 
      const offsetTop = this.getOffsetTop(this.start);

      // dom
      const classNames = [
        '.q-table__body-wrapper',
        '.q-table__fixed-right .q-table__fixed-body-wrapper',
        '.q-table__fixed .q-table__fixed-body-wrapper',
      ];
      classNames.forEach((className) => {
        const el = this.$el.querySelector(className);
        if (!el) return;

        // wrapElinnerEl
        if (!el.wrapEl) {
          const wrapEl = document.createElement('div');
          const innerEl = document.createElement('div');
          wrapEl.appendChild(innerEl);
          innerEl.appendChild(el.children[0]);
          el.insertBefore(wrapEl, el.firstChild);
          el.wrapEl = wrapEl;
          el.innerEl = innerEl;
        }

        if (el.wrapEl) {
          // 
          el.wrapEl.style.height = wrapHeight + 'px';
          // transform
          el.innerEl.style.transform = `translateY(${offsetTop}px)`;
          // paddingTop
          // el.innerEl.style.paddingTop = `${offsetTop}px`
        }
      });
    },

    // 
    updatePosition() {
      this.timer && clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.timer && clearTimeout(this.timer);
        // false
        this.handleScroll(false);
      }, this.throttleTime + 10);
    },

    // 
    update() {
      console.log('update');
      this.handleScroll();
    },

    // 
    scrollTo(index, stop = false) {
      const item = this.data[index];
      if (item && this.scroller) {
        this.updateSizes();
        this.calcRenderData();

        this.$nextTick(() => {
          const offsetTop = this.getOffsetTop(index);
          this.scroller.scrollTop = offsetTop;

          // scrollTo
          if (!stop) {
            setTimeout(() => {
              this.scrollTo(index, true);
            }, 50);
          }
        });
      }
    },

    // 
    reset() {
      this.sizes = {};
      this.scrollTo(0, false);
    },
  },
};
</script>
