<template>
  <div>
    <q-button @click="drawer = true" type="text">  </q-button>

    <q-drawer
      title=""
      size="800px"
      :wrapperClosable="false"
      :visible.sync="drawer"
    >
      <!-- mock -->
      <q-tabs v-model="activeMode" type="card" :before-leave="onBeforeTabLeave">
        <q-tab-pane label="" name="table">
          <DynamicTable
            v-if="activeTableTab"
            ref="dynamicTableRef"
            :table-head-data="tableHeadData"
            :data-ids="tableDataIds"
            :is-custom-row="isCustomRow"
            :disabled="false"
          />
        </q-tab-pane>
        <q-tab-pane label="Json" name="json">
          <JsonEditor ref="jsonEditorRef" :data-ids="tableDataIds" />
        </q-tab-pane>
      </q-tabs>
      <span slot="footer" class="dialog-footer">
        <q-button type="primary" @click="handleSave"></q-button>
      </span>
    </q-drawer>
  </div>
</template>


<script >
import { ref, getCurrentInstance } from '@vue/composition-api';
import DynamicTable from './components/dynamic-table';
import JsonEditor from './components/json-editor';
const __sfc_main = {
  name: 'EventSetter'
};
__sfc_main.props = {
  value: {
    type: String,
    default: ''
  },
  onChange: {
    type: Function,
    default: () => {}
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const _value = ref(props.value);
  const {
    proxy
  } = getCurrentInstance();
  const drawer = ref(false);
  const activeMode = ref('table');
  const tableHeadData = ref([{
    label: '',
    prop: 'name',
    type: 'text',
    required: true,
    component: 'CustomSelect',
    componentProps: {
      options: [{
        label: '1',
        value: '1'
      }, {
        label: '2',
        value: '2'
      }]
    }
  }]);
  const tableDataIds = ref([]);
  const isCustomRow = ref(true);
  const activeTableTab = ref(true);
  const dynamicTableRef = ref(null);
  const jsonEditorRef = ref(null);
  const onBeforeTabLeave = async (to, from) => {
    // 
    if (to === 'table' && from === 'json') {
      const isOk = jsonEditorRef.value.validate();
      if (!isOk) {
        proxy.$message.error('JSON!');
        return false;
      }
      const data = jsonEditorRef.value.getValues();
      activeTableTab.value = false;
      await nextTick();
      activeTableTab.value = true;
      await nextTick();
      dynamicTableRef.value.setValues(data);
    } else if (to === 'json' && from === 'table') {
      // JSON
      const data = dynamicTableRef.value.getValues();
      jsonEditorRef.value.setValues(data);
    }
    return true;
  };
  const handleSave = () => {
    const data = dynamicTableRef.value.getValues();
    const json = JSON.stringify(data, null, 2);
    props.onChange(json);
    drawer.value = false;
  };
  return {
    drawer,
    activeMode,
    tableHeadData,
    tableDataIds,
    isCustomRow,
    activeTableTab,
    dynamicTableRef,
    jsonEditorRef,
    onBeforeTabLeave,
    handleSave
  };
};
__sfc_main.components = Object.assign({
  DynamicTable,
  JsonEditor
}, __sfc_main.components);
export default __sfc_main;
</script>
<style lang="scss" scoped>
::v-deep .q-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  .q-tabs__header {
    flex: 0;
  }
  .q-tabs__content {
    flex: 1;
    .q-tab-pane {
      height: 100%;
    }
  }
}
</style>


